<template>
    <div>Microblog</div>
</template>

<script>
import {reactive,toRefs,onMounted,onUnmounted} from '@vue/composition-api'
export default {
  setup (props,{root}) {
    const state = reactive({

    })
    onMounted (() =>{
      root.$store.dispatch('hideNav') //初始化隐藏
    })
    onUnmounted (() =>{
      root.$store.dispatch('showNav') //离开显示
    })
    return {
      ...toRefs(state)
    }
  }
}
</script>

